.App {
  text-align: center;
}

.Container {
	max-width: 1230px;
	margin: auto;
}

 .Upperhead{
	background: #edeff0;
	border-bottom: 1px solid #ddd;
	padding: 8px 0; 
}

 .Upperhead .Container{
	display: flex;
	flex-direction: row;
	justify-content: space-between;
}

 .Upperhead .Container ul{
	list-style: none;
	display: flex;
	flex-direction: row;
	align-items: center;
	padding: 0;
	margin: 0;
}

 .Upperhead .Container ul li i {
	font-size: 18px;
	margin-right: 5px;
}

 .Upperhead .Container ul li a {
	text-decoration: none;
	color: #767676;
	font-family: RR;
	font-size: 13px;
	margin-left: 15px;
	display: flex;
	flex-direction: row;
	align-items: center;
}

/* Mid header */
.Midhead {
	border-bottom: 1px solid #ddd;
	/* padding: 10px 0; */
}

.Midhead .Container {
	display: flex;
	flex-direction: row;
	align-items: center;
}

.Midhead .Container .Bar{
	color: #767676;
	font-size: 35px;
	font-weight: lighter;
	border-left: 1px solid #ddd;
	border-right: 1px solid #ddd;
	padding: 15px 15px 10px;
}

.Midhead .Container .Logo {
	padding: 0 30px;
}

.Midhead .Container .Search-container{
	color: #767676;
	border: 1px solid #ddd;
	padding: 0 0 0 8px;
	width: 675px;
	border-radius: 2px;
	display: flex;
	flex-direction: row;
	align-items: center;
	justify-content: space-between;
}

.Midhead .Container .Search-container i{
	font-size: 22px;
	margin-right: 10px;
}

.Midhead .Container .Search-container .Input{
	display: flex;
	align-items: center;
}

.Midhead .Container .Search-container input{
	outline: none;
	border: none;
	font-size: 14px;
	font-family: RR;
}

.Midhead .Container .Search-container button{
	outline: none;
	border: none;
	background: #e62e04;
    color: #fff;
    font-size: 13px;
    cursor: pointer;
    text-transform: uppercase;
    border-top-right-radius: 2px;
	border-bottom-right-radius: 2px;
	padding: 12px 20px;
}

.Midhead .Container .Sign-in{
	display: flex;
    flex-direction: row;
	color: #767676;
	font-family: RR;
}

.Midhead .Container .Sign-in i{
	padding: 5px 5px 0;
	font-size: 25px;
}

.Midhead .Container .Sign-in p, h3{
	margin: 0;
}

.Midhead .Container .Sign-in h3{
	font-size: 15px;
}

.Midhead .Container .Sign-in p{
	font-size: 13px;
}

.Midhead .Container .Cart{
	color: #767676;
    font-size: 34px;
    padding: 10px 37px;
}


/* Navigation */
 .Nav {
	border-bottom: 1px solid #ddd;
}

.Nav .Container ul {
	display: flex;
	flex-direction: row;
	list-style: none;
	padding: 0;
	margin: 0;
}

.Nav .Container ul li {
	border-left: 1px solid #ddd;
	padding: 8px 6px;
}

.Nav .Container ul li a{
	text-decoration: none;
	color: #444;
	font-size: 14px;
	font-family: RR;
	position: relative;
}

.Nav .Container ul li:hover{
    border-bottom: solid 1px #92685e;
    background: #f1e5e5;
    transition: 1s;
}

.Nav .Container ul .Sale a{
	border-left: none;
	background: #db3c48;
	color: #ffffff;
	font-family: RB;
}

.Nav .Container ul .Sale{
	border-left: none;
	background: #db3c48;
	color: #ffffff;
	padding: 8px;
}

sup {
	position: absolute;
    top: -9px;
    right: -5px;
	color: #fff;
	background: #d71a21;
	padding: 0 2px 1px 4px;
	font-size: 9px;
	border-radius: 2px;
}

/* PRODUCT SECTION */

.Product-container{
    display: flex;
    justify-content: center;
    height: auto;
}
.Product-container section{
    margin: 0px 6.3%;
    background-color: #ffffff;
    height: auto;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(265px, 1fr));
    justify-items: center;
    grid-gap: 0.5em;
    width: 100%;
    padding-top: 15px;
}

/* PRODUCT LIST */

.Product-list{
    display: grid;
    grid-row: initial;
    justify-items: center;
    align-content: center;
    background-color: #ffffff;
    border: 1px solid #dddddd;
    width: 290px;
    height: 375px;
    grid-row-gap: 1em;
}

.Product-name span{
    font-size: 0.7em;
    color: grey;
}
.Product-name{
    padding: 0px 10px;
    text-align: center;
    margin: -15px 0px; 
}

.Product-bottom{
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 96%;
}
.Product-bottom p{
    margin: 0px;
    font-size: 1.2em;
    color: #d02f37;
    font-weight: 500;
}
.Product-bottom strike{
    font-size: 0.8em;
    color: grey;
}
.Product-price{
    text-align: left;
}
.Button-div button{
    color: white;
    background-color: #f55100;
    width: 155px;
    height: 45px;
    border: none;
    cursor: pointer;
}
.Button-div button:hover{
    background-color: #e24f06;
}